import React from "react";
import Layout from "../../../layout";
import { Card } from "../../../components";
import { FaFileAlt, FaFileArchive, FaUsers } from "react-icons/fa";
import { PrivatePaths } from "../../../routes/path";
import { usePartner } from "../Partner/hooks";
import { useAttendees } from "../Attendees/hooks";
import { useAdmin } from "../Admin/hooks";

const Dashbboard = () => {
  const partners = usePartner();
  const attend = useAttendees();
  const admin = useAdmin();
  return (
    <Layout name="Dashboard">
      <div className="cardFlex">
        <Card
          Icon={FaFileAlt}
          title="Registered Partners"
          count={partners?.length}
          url={PrivatePaths.PARTNERS}
        />
        <Card
          Icon={FaFileArchive}
          title="Registered Webinar Attendees"
          count={attend?.length}
          url={PrivatePaths.ATTEND}
        />
        <Card
          Icon={FaUsers}
          title="Admins"
          count={admin?.length}
          url={PrivatePaths.ADMIN}
        />
      </div>
    </Layout>
  );
};

export default Dashbboard;
